.header{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  background-color: rgb(89, 35, 120, 0.8);
  padding: 1vw;
  height: 7vh;
}

.footer{
  display: flex;
  flex-direction: column;
}

.menu{
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.menuBtn{
  background-color: rgb(89, 35, 120);
  margin: 2vw;
  border: 0;
  font-size: 3vh;
  color: rgb(29, 11, 35);
  font-weight:300;
  font-family: fantasy;
}

.menuBtn:hover{
  color: rgb(221, 0, 255);
}

.app{
  display: flex;
}

.logo{
  font-family: 'Times New Roman', Times, serif;
  font-size: larger;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size:5vh;
}

.infoBtn{
  background-color: rgb(89, 35, 120);
  border: 1px solid black;
  border-radius: 50%; 
  font-size: 4vh;
  width: 6vh;
  height: 6vh;
  font-weight: 700;
}
.mm{
  display: flex;
  overflow: hidden;
  position: absolute;
}

.mm img {
  transition: transform 1s ease-in-out; 
  height: 50vh;
  width: auto;
}

body{
  background-color: rgb(48, 48, 48);
}

.mapUtil{
  display: flex;
  height: 1000px;
  width: auto;
  border: 20px solid red;
}